<template>
	<div>
		<div class="secction diyscrollbar">
			<list-top placeholder="/" btnText="" v-on:addBtn="addArticle" v-on:search="searchArticleAction"></list-top>
			<articles-list v-on:del="delArticle" v-on:childGetArticles="getArticles" v-on:publish="publishArtcile" v-on:amend="amendArticle" v-on:read="readArticle"  v-bind:page="page" v-bind:articles="articlesList"></articles-list>
		</div>
		<el-dialog v-bind:title="dialog.title" @close="closeDialog" v-model="dialog.control" top="10px" size="full">
			<article-write  v-on:childGetArticles="getArticles" v-bind:page="page" v-on:close="closeDialog" v-bind:article="dialog.article"></article-write>
		</el-dialog>
	</div>
</template>

<script>
	import articleWrite from './article/ArticleWrite.vue'
	import listTop from './public/ListTop.vue'
	import articlesList from './article/ArticlesList.vue'
	export default {
		name: 'articlesList',
		components: {
			'list-top': listTop,
			'articles-list': articlesList,
			'article-write': articleWrite,
		},
		data() {
			return {
				dialog: {
					article: {},
					title: '',
					control: false, //dialog
				},
				articlesList:[],
				page: {
					currentPage: 1, //
					totalCourses: 0, //
					currentNum: 30, //
					searchText: '',
				}
			}
		},
		created() {
			this.getArticles(this.page.currentPage, this.page.currentNum, this.page.searchText);
		},
		methods: {
			setCurrentPage(num) {
				this.page.currentPage = num;
			},
			setCurrentNum(num) {
				this.page.currentNum = num;
			},
			setSearchText(keyText) {
				this.page.searchText = keyText;
			},
			getArticles(page, rows, search) { //
				this.currentNum = rows;
				/*this.$http.get("../../../../static/url/articles.json").then((response) => {
					console.log(response.data)
					this.articlesList = response.data;
					//
					for(let i = 0; i < this.articlesList.length; i++) {
						(function(self,articles) {
							console.log(articles[i]);
							self.$http.get("admin/article/getTags/" + articles[i].id).then((response) => {
								console.log(response.data);
								articles[i].tagSet = response.data;
							}, (response) => {
								console.log("");
								articles[i].tagSet = [{
									"name": ""
								}];
							});
						})(this,this.articlesList);
					}
				}, (response) => {
					this.$message.error('');
				});*/
				
				this.$http.get("/admin/article/getArticlesByPage?searchStr=" + search + "&page=" +  (page-1) + "&size=" + rows).then((response) => {
					console.log(response.data);
					this.articlesList=response.data.content;
					//
					for(let i = 0; i < this.articlesList.length; i++) {
						(function(self,articles) {
							console.log(articles[i]);
							self.$http.get("/admin/article/getTags/" + articles[i].id).then((response) => {
								console.log(response.data);
								articles[i].tagSet = response.data;
							}, (response) => {
								console.log("");
								articles[i].tagSet = [{
									"name": ""
								}];
							});
						})(this,this.articlesList);
					}
				}, (response) => {
					this.$message.error('');
				});
				/*this.$http.get("/admin/article").then((response) => {
					console.log(response.data);
					this.articlesList=response.data;
					console.log(this.articlesList);
					//
					for(let i = 0; i < this.articlesList.length; i++) {
						(function(self,articles) {
							self.$http.get("/admin/article/getTags/" + articles[i].id).then((response) => {
								console.log(response.data);
								articles[i].tagSet = response.data;
							}, (response) => {
								console.log("");
								articles[i].tagSet = [{
									"name": ""
								}];
							});
						})(this,this.articlesList);
					}
				}, (response) => {
					this.$message.error('');
				});*/
			},
			publishArtcile(a){
				this.$http.post('/admin/article/release', {
					"id": a.id,
					"title": a.title,
					"tagSet": a.tagSet,
					"mdContent": a.mdContent,
				}).then((response) => {
					//2.state
					this.getArticles(this.page.currentPage, this.page.currentNum, this.page.searchText);
					this.$message.success('');
					
				}, (response) => {
					this.$message.error('');
				});
			},
			readArticle() {

			},
			addArticle() { //dialog
				this.dialog.article = {
					id:'',
					title: '',
					tagSet: [],
					state: 1,
					content: '',
				};
				this.openDialog();
				this.dialog.title = "";
				//if(testEditor.settings === undefined) {
					createdEditormd('editormd', ''); //Editor.md
				//} else {
					setTimeout(function() {
						testEditor.clear();
						//testEditor.insertValue(article);
					}, 1)
				//}

			},
			amendArticle(article) { //dialog
				this.dialog.article = article;
				this.openDialog();
				this.dialog.title = "";
				console.log(article.mdContent);
				//if(testEditor.settings === undefined) {
					createdEditormd('editormd', JSON.parse(JSON.stringify(article.mdContent))); //Editor.md
				//} else {
					setTimeout(function() {
						testEditor.clear();
						testEditor.insertValue(JSON.parse(JSON.stringify(article.mdContent)));
					}, 1)
				//}

			},
			delArticle(article) {
				this.$confirm('' + article.title + ', ?', '', {
					confirmButtonText: '',
					cancelButtonText: '',
					type: 'warning'
				}).then(() => {
					console.log(article.id);
					this.$http.delete("/admin/article/" + article.id).then((response) => {
						if(response.data) {
							this.$message.success('');
							this.getArticles(this.page.currentPage, this.page.currentNum, this.page.searchText);
						} else {
							this.$message.error('');
						}
					}, (response) => {
						this.$message.error('');
					});
				}).catch(() => {
					this.$message({
						type: 'info',
						message: ''
					});
				});
			},
			searchArticleAction(searchKey) { //

			},
			closeDialog() {
				this.dialog.control = false;
				//testEditor.editor.remove();
			},
			openDialog() {
				this.dialog.control = true;
			},
		},
	}
</script>

